.subscription__item-wrapper > span {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #102654;
  margin-bottom: 65px;
}
@media (max-width: 767px) {
  .subscription__item-wrapper > span {
    margin-bottom: 54px;
  }
}
@media (hover: hover) {
  .subscription__item-wrapper:hover:not(.focus-visible) {
    outline: 7px solid linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
    outline-offset: -7px;
  }
}

.subscription__price {
  display: flex;
  align-items: center;
  column-gap: 14px;
  margin-bottom: 33px;
  position: relative;
}
@media (max-width: 767px) {
  .subscription__price {
    column-gap: 10px;
    margin-bottom: 29px;
  }
}
.subscription__price > span {
  font-weight: 700;
  font-size: 60px;
  line-height: 1.5;
  color: #103aae;
  z-index: 1;
}
@media (max-width: 767px) {
  .subscription__price > span {
    font-size: 48px;
  }
}
.subscription__price > span + span {
  font-weight: 700;
  font-size: 100px;
  line-height: 1.5;
  color: #e8e8e8;
  position: absolute;
  z-index: 0;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%);
}
@media (max-width: 767px) {
  .subscription__price > span + span {
    font-size: 80px;
  }
}
.subscription__price > svg {
  z-index: 1;
}
@media (max-width: 767px) {
  .subscription__price > svg {
    width: 24px;
  }
}

.subscription__button {
  min-width: 208px;
  padding: 27px 20px 25px;
  margin-top: auto;
}
@media (hover: hover) {
  .subscription__button:hover:not(.focus-visible) {
    background-image: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
    outline: none;
    color: #ffffff;
    opacity: 1;
  }
}

.promo {
  height: 1000px;
}

